import apiHelper from './apiHelper'

export default {
  /**
   * 获取微信授权后的用户阅读信息
   */
  getUserReadInfo(params) {
    return apiHelper.pureGet('/apiv1/user/readInfo', {
      ...params
    })
  },
  // 设置阅读时长
  setReadSeconds(params) {
    return apiHelper.purePost('/apiv1/user/setReadSeconds', {
      ...params
    })
  }
}
