import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Center from './views/Center.vue'
import wxShare from './utils/wxShare'
import store from './store'
import wxShareNew from "./utils/wxShareNew";

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior(to, from, savedPosition) {
    //页面加载出来后将页面滚动到顶部，避免微信缓存了前面列表页的滚动位置导致进入详情页后自动滚动到底部的问题
    if (to.name === 'articleList' || to.name === 'learned' ) {
      return {x: 0, y: 0}
    }
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        keepAlive: true,  // 需要缓存true
        navShow: true, // true显示，false隐藏
      },
      beforeRouteEnter(to, from, next) {
        next();
      }
    },
    {
      path: '/center',
      name: 'center',
      component: Center,
      meta: {
        keepAlive: true,  // 需要缓存true
        navShow: true, // true显示，false隐藏
      }
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('./views/About.vue'),
      meta: {
        keepAlive: true,  // 需要缓存true
        navShow: false, // true显示，false隐藏
      }
    },
    {
      path: '/rank',
      name: 'rank',
      component: () => import('./views/rank.vue'),
      meta: {
        keepAlive: false,  // 需要缓存true
        navShow: false, // true显示，false隐藏
      }
    },
    {
      path: '/groupCode',
      name: 'groupCode',
      component: () => import('./views/GroupCode.vue'),
      meta: {
        keepAlive: true,  // 需要缓存true
        navShow: false, // true显示，false隐藏

      }
    },

    {
      path: '/article/:id',
      name: 'article',
      component: () => import('./views/Article.vue'),
      meta: {
        keepAlive: false,  // 需要缓存true
        navShow: false, // true显示，false隐藏

      }
    },
    {
      path: '/articleList',
      name: 'articleList',
      component: () => import('./views/ArticleList.vue'),
      meta: {
        keepAlive: false,  // 需要缓存true
        navShow: false, // true显示，false隐藏
      }
    },
    {
      path: '/learned',
      name: 'learned',
      component: () => import('./views/Learned.vue'),
      meta: {
        keepAlive: false,  // 需要缓存true
        navShow: false, // true显示，false隐藏

      }
    },
    {
      path: '/payedArticle/:id',
      name: 'payedArticle',
      component: () => import('./views/PayedArticle.vue'),
      meta: {
        keepAlive: false,  // 需要缓存true
        navShow: false, // true显示，false隐藏

      }
    },
    {
      path: '/wxAuth',
      name: 'wAuth',
      component: () => import('./views/WxAuth.vue'),
      meta: {
        keepAlive: false,  // 不需要缓存
        navShow: false, // true显示，false隐藏

      },
      /*beforeRouteLeave(to, from, next) {
        // 设置下一个路由的 meta
        // to.meta.keepAlive = true;  // 让 A 缓存，即不刷新
        next();
      }*/
    },
  ],
});

router.beforeEach((to, from, next) => {
  // 非微信手机端浏览器直接进入页面, 注释即可
  if (!/micromessenger/i.test(navigator.userAgent)) {
    next()
    return
  }
  //不要对 WxAuth 路由进行拦截，不进入 WxAuth 路由就拿不到微信返回的授权 code
  if (to.name === 'wxAuth') {
    next()
    return
  }
  
  // let wxUserInfo = localStorage.getItem('wxUserInfo');
  let wxUserInfo = sessionStorage.getItem('wxUserInfo');

  if (!wxUserInfo) {
    //保存当前路由地址，授权后还会跳到此地址
    sessionStorage.setItem('wxRedirectUrl', to.fullPath);
    //请求微信授权,并跳转到 /WxAuth 路由
    // let appId = '测试服AppId'
    let appId = 'wx2bac5a2f552cd30b';
    let redirectUrl = encodeURIComponent('http://wedding.fecstec.com/wxAuth');
    //判断是否为正式环境
    if (window.location.origin.indexOf('http://wedding.fecstec.com') !== -1) {
      // appId = '正式服AppId'
      appId = 'wx2bac5a2f552cd30b';
      redirectUrl = encodeURIComponent('http://wedding.fecstec.com/wxAuth');
    }
    // 应用授权作用域，snsapi_base （不弹出授权页面，直接跳转，只能获取用户openid），snsapi_userinfo （弹出授权页面，可通过openid拿到昵称、性别、所在地。并且，即使在未关注的情况下，只要用户授权，也能获取其信息）
    window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appId}&redirect_uri=${redirectUrl}&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`
  } else {
    next()
  }
});

// 每个页面都注入jssdk
router.afterEach((to, from) => {
  //微信分享前初始化 wxConfig
  // wxShare.initConfig(location.origin + '/dc' + to.fullPath)
  wxShare.initConfig(window.location.origin + '' + to.fullPath)
});

export default router
