import { SET_FIRSTLINK, SET_NAVBAR, SET_WXREDIRECTURL } from '../mutation-types'
const common = {
    state: {
      firstLink: '',
      showNavBar: true,
      wxRedirectUrl: '',
    },
    actions: {},
    mutations: {
        [SET_FIRSTLINK](state, firstLink) {
            localStorage.setItem('firstLink', firstLink);
            state.firstLink = firstLink;
        },
        [SET_NAVBAR](state, showNavBar) {
            state.showNavBar = showNavBar;
        },
        [SET_WXREDIRECTURL](state, wxRedirectUrl) {
            state.wxRedirectUrl = wxRedirectUrl;
        }
    }
};
export default common