/* eslint-disable */
import axios from 'axios';
import wx from 'weixin-js-sdk';
import store from "../store";

const _config = {
  wx_error: '',
  jsSDKAuth: '',
  shareSign: {
    appid: "",
    jsapi_ticket: "",
    nonceStr: "",
    signature: "",
    timestamp: '',
    url: "",
  },
  defaultWxShareConfig: {
    title: "微信公众号英文能力",
    desc: "关注微信公众号英文能力坚持提高英语水平",
    link: location.href.split("#")[0],
    imgUrl: 'https://res.wx.qq.com/a/wx_fed/weixin_portal/res/static/img/dNEBuK6.png',
    jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'hideMenuItems', 'closeWindow', 'onMenuShareQQ',
      'onMenuShareWeibo',
      'onMenuShareQZone',],
    hideMenuList: ['menuItem:favorite', 'menuItem:editTag', 'menuItem:delete', 'menuItem:originPage', 'menuItem:readMode', 'menuItem:openWithQQBrowser', 'menuItem:openWithSafari', 'menuItem:share:email', 'menuItem:share:brand', 'menuItem:share:qq', 'menuItem:share:QZone', 'menuItem:share:facebook', 'menuItem:share:weiboApp']
  }, // 'menuItem:copyUrl',
  wxShareConfig: {}
};

// 创建微信分享对象
function WXShare(config) {
  this.config = config || _config;
}

// 验证签名
WXShare.prototype.wxShareAuth = function (sign_url) {
  let vm = this;
  if (!sign_url) {
    console.error("缺少授权页面url");
    return;
  }
  let params = {
    signurl: sign_url
  }
  console.log(vm.config.jsSDKAuth)
  return axios.post(vm.config.jsSDKAuth, params).then(res => {
    //更新分享授权签名参数
    let {appId, nonceStr, signature, timestamp} = res;
    vm.config.shareSign.appid = appId;
    // vm.config.shareSign.jsapi_ticket=jsapi_ticket;
    vm.config.shareSign.nonceStr = nonceStr;
    vm.config.shareSign.signature = signature;
    vm.config.shareSign.timestamp = timestamp;
    // vm.config.shareSign.url=url;
    return res;
  }).catch((err) => {
    console.log(err.response);
  })
};

// 初始化jssdk配置
WXShare.prototype.initWxShare = function (sign_url) {
  if (!/micromessenger/i.test(navigator.userAgent)) {
    return
  }
  // 如果是 iOS 设备，则使用第一次进入App时的 URL 去请求 wxConfig，不然的话会导致 iOS 中分享的链接不对 ios环境下，微信只认第一次进来的路由啊！
  if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
    //记录第一次进入时的链接，iOS 分享时需要用到
    if (!store.state.firstLink) {
      store.commit('SET_FIRSTLINK', sign_url)
    } else {
      sign_url = store.state.firstLink
    }
  }
  let vm = this;
  this.wxShareAuth(sign_url).then((res) => {
    vm.config.wxShareConfig = Object.assign({}, vm.config.defaultWxShareConfig);
    alert('vm')
    vm.configWXJSSDK();
  });
};

// 配置分享类容
WXShare.prototype.updateWxShareConfig = function (shareConfig) {
  console.log(shareConfig);
  if (!shareConfig) return;
  let {title, desc, link, imgUrl, jsApiList, hideMenuList} = shareConfig;
  let defaultWxShareConfig = this.config.defaultWxShareConfig;
  this.config.wxShareConfig.title = title || defaultWxShareConfig.title;
  this.config.wxShareConfig.desc = desc || defaultWxShareConfig.desc;
  this.config.wxShareConfig.link = link || defaultWxShareConfig.link;
  this.config.wxShareConfig.imgUrl = imgUrl || defaultWxShareConfig.imgUrl;
  this.config.wxShareConfig.jsApiList = jsApiList || defaultWxShareConfig.jsApiList;
  this.config.wxShareConfig.hideMenuList = hideMenuList || defaultWxShareConfig.hideMenuList;
  //重新初始化
  // this.configWXJSSDK();
  this.startShare();
};

// 重置分享配置参数
WXShare.prototype.resetWxShareConfig = function () {
  let {title, desc, link, imgUrl, jsApiList, hideMenuList} = this.config.defaultWxShareConfig;
  this.config.wxShareConfig.title = title;
  this.config.wxShareConfig.desc = desc;
  this.config.wxShareConfig.link = link;
  this.config.wxShareConfig.imgUrl = imgUrl;
  this.config.wxShareConfig.jsApiList = jsApiList;
  this.config.wxShareConfig.hideMenuList = hideMenuList;

  this.configWXJSSDK();
};

WXShare.prototype.updateWxShareSign = function (data) {

};

// 配置
WXShare.prototype.configWXJSSDK = function () {
  let shareSign = this.config.shareSign;
  let shareConfig = this.config.wxShareConfig;
  wx.config({
    debug: false,
    appId: shareSign.appid,
    timestamp: shareSign.timestamp,
    nonceStr: shareSign.nonceStr,
    signature: shareSign.signature,
    jsApiList: shareConfig.jsApiList
  });
  wx.ready(function(){
    console.log('conf ok')
    wx.checkJsApi({
      jsApiList: shareConfig.jsApiList,
      success: function (res) {
        //alert(JSON.stringify(res));
        //alert(JSON.stringify(res.checkResult.getLocation));
        if (res.checkResult.getLocation == false) {
          alert('你的微信版本太低，不支持微信JS接口，请升级到最新的微信版本！');
          return;
        }
      }
    });
    // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
  });
  wx.error((res) => {
    console.log(res);
    // wx.closeWindow();
    // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
  });
}


// 开始分享
WXShare.prototype.startShare = function () {
  if (!/micromessenger/i.test(navigator.userAgent)) {
    return
  }
  let vm = this;
  wx.ready(function () {
    let wxShareConfig = vm.config.wxShareConfig;
    let {title, desc, link, imgUrl, hideMenuList} = wxShareConfig;
    // 如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行
    // 在 weixin-js-sdk 1.4 之后，分享到朋友和朋友圈要分别使用 updateAppMessageShareData，updateTimelineShareData
    // 这里为了兼容老版本的微信，所以把以前的老版本的方法也写上

    // 微信用户或群分享
    wx.updateAppMessageShareData({
      title,
      desc,
      link,
      imgUrl,
      success: function () {
        //分享成功回调，分享成功后要做的事情可以写在这里，比如说上传分享成功信息给服务器，方便做统计
        alert("分享成功");
      }
    });
    // 分享给朋友，此方法即将被废弃，改用 updateAppMessageShareData
    wx.onMenuShareAppMessage({
      title,
      desc,
      link,
      imgUrl,
      success: function () {
        alert("分享成功");
      },
      fail: function () {
        alert("分享失败");
      },
      cancel: function () {
        alert("取消分享");
      }
    });
    // 朋友圈分享
    wx.updateTimelineShareData({
      title,
      link,
      imgUrl,
      success: function () {
        alert("分享成功");
      },
      cancel: function () {
        alert("取消分享");
      }
    });
    wx.onMenuShareTimeline({
      title,
      link,
      imgUrl,
      success: function () {
        alert("分享成功");
      },
      cancel: function () {
        alert("取消分享");
      }
    });
    // 分享给qq
    wx.onMenuShareQQ({
      title,
      link,
      imgUrl,
      success: function () {
        alert("分享成功");
      },
      cancel: function () {
        alert("取消分享");
      }
    });
    //分享给腾讯微博
    wx.onMenuShareWeibo({
      title,
      link,
      imgUrl,
      success: function () {
        alert("分享成功");
      },
      cancel: function () {
        alert("取消分享");
      }
    })
    //分享给qq空间
    wx.onMenuShareQZone({
      title,
      link,
      imgUrl,
      success: function () {
        alert("分享成功");
      },
      cancel: function () {
        alert("取消分享");
      }
    })
    wx.hideMenuItems({
      menuList: hideMenuList
    })
  });
/*  wx.error(function (res) {
    alert("分享失败: error");
    console.log(res)
    // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
  });*/
}

const wxShare = new WXShare();
export default wxShare;
