<template>
  <div id="app" class="gradient">
    <!--keep-alive缓存页面包括数据, 不缓存WxAuth keepAlive的属性 include，exclude="WxAuth"-->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
      </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive">
    </router-view>
    <van-tabbar v-if="$route.meta.navShow" v-model="active" :route="routeType" :border="showBorder" v-show="showNavBar" active-color="#3dcc97" class="own-nav-b" safe-area-inset-bottom>
      <van-tabbar-item to="/"
                       :replace="replaceNow">
        <span>学习</span>
        <img slot="icon" class="own-tab-icon"
                slot-scope="props"
                :src="props.active ? homeIcon.active : homeIcon.normal">
      </van-tabbar-item>
      <!--<van-tabbar-item icon="column" to="/about/1">test</van-tabbar-item>-->
      <van-tabbar-item to="/center"
                       :replace="replaceNow">
        <span>我的</span>
        <img slot="icon" class="own-tab-icon"
                slot-scope="props"
                :src="props.active ? centerIcon.active : centerIcon.normal">
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
  import {mapGetters} from "vuex";
  export default {
    computed: {
      ...mapGetters(["showNavBar"])
    },
    data() {
      return {
        active: 0,
        routeType: true,
        showBorder: false,
        replaceNow: false,
        homeIcon: {
          // normal: 'http://bkcdn.fecstec.com/1/20190509/14275178336be1.png',
          normal: 'http://bkcdn.fecstec.com/1/20190509/1438014047621f.png',
          // active: 'http://bkcdn.fecstec.com/1/20190509/14273620893d06.png'
          active: 'http://bkcdn.fecstec.com/1/20190509/14374113369439.png'
        },
        centerIcon: {
          // normal: 'http://bkcdn.fecstec.com/1/20190509/1428176818770f.png',
          normal: 'http://bkcdn.fecstec.com/1/20190509/1438396864a26e.png',
          // active: 'http://bkcdn.fecstec.com/1/20190509/1428061263b097.png'
          active: 'http://bkcdn.fecstec.com/1/20190509/14382070769b4b.png'
        }
      }
    },
    created() {
      if(this.$route.path === '/') {
        this.active = 0;
      }
      if(this.$route.path === '/center') {
        this.active = 1;
      }
    },
    mounted() {
      // 关闭浏览器窗口的时候清空浏览器缓存在localStorage的数据
      /*window.onbeforeunload = function (e) {
        alert('ok')
        window.localStorage.clear()
      }*/
    },
    methods: {
      changTabbar(active) {
        alert(active)
        if(active === 0){
          this.$router.replace('/')
        }else{
          this.$router.replace('/center')
        }

      }
    }
  }
</script>
