import {SET_USERINFO, SET_TOKEN} from '../mutation-types'
import {getToken, setToken, removeToken} from '../../utils/auth'
import Vue from 'vue'
const common = {
  state: {
    userInfo: JSON.parse(localStorage.getItem('wxUserInfo')),
    token: getToken(),
  },
  actions: {
    // remove token
    resetToken({commit}) {
      return new Promise(resolve => {
        commit('SET_TOKEN', '');
        removeToken();
        resolve()
      })
    },
    loginByCode({commit}, code) {
      return new Promise((resolve, reject) => {
        Vue.apis.public.getWxUserInfo(code).then(response => {
          console.log(response)
          const { data } = response
          localStorage.setItem('openid', data.userInfo.openid);
          commit('SET_USERINFO', data.userInfo);
          commit('SET_TOKEN', data.token);
          setToken(data.token); // 保存token
          localStorage.setItem('wxUserInfo', JSON.stringify(data.userInfo));
          resolve(response)
        }).catch(error => {
          reject(error)
        })
      })
    }
  },
  mutations: {
    [SET_USERINFO](state, userInfo) {
      state.userInfo = userInfo;
    },
    [SET_TOKEN](state, token) {
      console.log(token)
      state.token = token;
    }
  }
}
export default common