import wx from 'weixin-js-sdk'
import store from '../store/index'
import apiHelper from '../apis/apiHelper'

export default {
  initConfig (url) {
    if (!/micromessenger/i.test(navigator.userAgent)) {
      return
    }
    //如果是 iOS 设备，则使用第一次进入App时的 URL 去请求 wxConfig，不然的话会导致 iOS 中分享的链接不对
    if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
      //记录第一次进入时的链接，iOS 分享时需要用到
      if (!store.state.firstLink) {
        store.commit('SET_FIRSTLINK', url)
      } else {
        url = store.state.firstLink
      }
    }
    // 将 url 传给后台请求微信签名配置
    apiHelper.purePost('http://cnnapi.ngrok.tecfcs.com/common/jsapi', {signurl: url}).then(res => {
      // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，
      if (res) {
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: res.appId,   // 必填，公众号的唯一标识
          nonceStr: res.nonceStr,   // 必填，生成签名的随机串
          signature: res.signature, // 必填，签名，见附录1
          timestamp: res.timestamp, // 必填，生成签名的时间戳
          jsApiList: [
            'checkJsApi',
            "updateAppMessageShareData",
            "updateTimelineShareData",
            "onMenuShareAppMessage",
            "onMenuShareTimeline",
            'scanQRCode',
            'playVoice',
            'pauseVoice',
            'onVoicePlayEnd',
            'stopVoice',
            'uploadVoice',
            'chooseImage',
            'previewImage',
            'downloadImage',
            'downloadVoice',
            'uploadImage',
            'chooseWXPay'
          ], // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        })
        wx.ready(function(){
          console.log('conf ok')
          wx.checkJsApi({
            jsApiList: [
              'onMenuShareTimeline',
              'onMenuShareAppMessage',
              'onMenuShareQQ',
              'onMenuShareWeibo',
              'onMenuShareQZone',
              'scanQRCode',
              'playVoice',
              'pauseVoice',
              'onVoicePlayEnd',
              'stopVoice',
              'uploadVoice',
              'chooseImage',
              'previewImage',
              'downloadImage',
              'downloadVoice',
              'uploadImage',
              'chooseWXPay'
            ],
            success: function (res) {
              //alert(JSON.stringify(res));
              //alert(JSON.stringify(res.checkResult.getLocation));
              if (res.checkResult.getLocation == false) {
                alert('你的微信版本太低，不支持微信JS接口，请升级到最新的微信版本！');
                return;
              }
            }
          });
          // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
        });
        wx.error((res) => {
          console.log(res);
          // wx.closeWindow();
          // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
        });
      }
    })
  },
  // 微信分享
  share (url, title, desc, imgUrl, pageType) {
    if (!/micromessenger/i.test(navigator.userAgent)) {
      return
    }
    //默认分享链接，比如说应用首页
    let shareUrl = location.origin + '/'
    if (url) {
      shareUrl = url
    }
    let shareTitle = '微信公众号英文能力'
    if (title) {
      shareTitle = title
    }
    let shareDesc = '关注微信公众号英文能力坚持提高英语水平'
    if (desc) {
      shareDesc = desc
    }
    let shareImg = '默认分享图片地址'
    if (imgUrl) {
      shareImg = imgUrl
    }

    /*
    * 微信分享会根据分享的不同,为原始链接拼接如下参数:
      朋友圈   from=timeline&isappinstalled=0
      微信群   from=groupmessage&isappinstalled=0
      好友分享 from=singlemessage&isappinstalled=0
    *
    * */

    wx.ready(() => {
      // 如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行
      // 在 weixin-js-sdk 1.4 之后，分享到朋友和朋友圈要分别使用 updateAppMessageShareData，updateTimelineShareData
      // 这里为了兼容老版本的微信，所以把以前的老版本的方法也写上

      // 微信用户或群分享
      wx.updateAppMessageShareData({
        title: shareTitle, // 分享标题
        desc: shareDesc, // 分享描述
        link: shareUrl, // 分享链接 默认以当前链接
        imgUrl: shareImg, // 分享图标
        success: ()=>{
          console.log('share success!')
          // 成功回调 新版已经不能回调了
          // successCallback(openid)
          //分享成功回调，分享成功后要做的事情可以写在这里，比如说上传分享成功信息给服务器，方便做统计
        },
       /* fail: (err) => {
          errorCallback(err)
        }*/
      })
      // 朋友圈分享
      wx.updateTimelineShareData({
        title: shareTitle,
        link: shareUrl,
        imgUrl: shareImg,
        success: ()=>{
          console.log('share success!')
          // 成功回调
          // 支付购买页面分享的
          if(pageType === 'payPage'){

          }
          // successCallback()
          //分享成功回调，分享成功后要做的事情可以写在这里，比如说上传分享成功信息给服务器，方便做统计
        },
       /* fail: (err) => {
          errorCallback(err)
        }*/
      })
      wx.onMenuShareAppMessage({ // 分享给朋友，此方法即将被废弃，改用 updateAppMessageShareData
        title: shareTitle,
        desc: shareDesc,
        link: shareUrl,
        imgUrl: shareImg,
        success: ()=>{
          console.log('share success!')
          // 成功回调
          // successCallback()
          //分享成功回调，分享成功后要做的事情可以写在这里，比如说上传分享成功信息给服务器，方便做统计
        },
       /* fail: (err) => {
          errorCallback(err)
        }*/

      })
      wx.onMenuShareTimeline({ //分享到朋友圈，此方法即将被废弃，改用 updateTimelineShareData
        title: shareTitle,
        link: shareUrl,
        imgUrl: shareImg,
        success: ()=>{
          console.log('share success!')
          // 成功回调
          // successCallback()
          //分享成功回调，分享成功后要做的事情可以写在这里，比如说上传分享成功信息给服务器，方便做统计
        },

      });
      // 隐藏分享栏按钮
      wx.hideMenuItems({ // 'menuItem:copyUrl',
        menuList: ['menuItem:favorite', 'menuItem:editTag', 'menuItem:delete', 'menuItem:originPage', 'menuItem:readMode', 'menuItem:openWithQQBrowser', 'menuItem:openWithSafari', 'menuItem:share:email', 'menuItem:share:brand', 'menuItem:share:qq', 'menuItem:share:QZone', 'menuItem:share:facebook', 'menuItem:share:weiboApp']
      });
      wx.error(res => {
        // config信息验证失败会执行error函数，如签名过期导致验证失败，
        // 具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
        console.log(res);
        // configErrorCallback(res)
      })
    })
  },
}



