
import apiHelper from "./apiHelper";



export default {
  /**
   * 获取微信授权后的用户信息
   */
  /*  getWxUserInfo (params) {
      return apiHelper.purePost('/common/authWechat',{
        ...params
      })
    },*/
  createOrder(params) {
    return apiHelper.post('/apiv1/order/createOrder', {
      ...params
    })
  },
  createArticleOrder(vueContext, isShow, params) {
    return apiHelper.post(vueContext, '/apiv1/order/createArticleOrder', {
      ...params
    }, isShow)
  },
  orderDoPay(vueContext, isShow, params) {
    return apiHelper.post(vueContext, '/apiv1/order/doPay', {
      ...params
    }, isShow)
  },
  articleDoPay(vueContext, isShow, params) {
    return apiHelper.post(vueContext, '/apiv1/order/articleDoPay', {
      ...params
    }, isShow)
  },
  articleDetail(params) {
    return apiHelper.purePost('/apiv1/payed/article/detail', {
      ...params
    })
  },
}