<template>
    <div class="user">
        <van-skeleton
                title
                :row="3"
                :loading="loading">
            <van-row class="user-poster">
                <van-col span="8">
                    <div class="poster">
                        <img class="poster-img" :src="userInfo.headimgurl" alt="">
                        <!--<img src="http://thirdwx.qlogo.cn/mmopen/vi_32/ajNVdqHZLLCRdWUftA1icg7mB9vicYxznHT64Yib0MTqlibcN2lFibZIszkQBibX2RUk6q46qfo8JRZrLVU92uv8xdibg/132" alt="">-->
                    </div>
                </van-col>
                <van-col span="16">
                    <van-row class="own-name-line">
                        <div class="poster-name">{{userInfo.nickname}}</div>
                    </van-row>
                </van-col>
            </van-row>
        </van-skeleton>

        <!--学习天数和学习文章数-->
        <!--<van-row class="user-attribute">
            <van-col :span="12">
                <div class="own-keep">
                    0
                </div>
                <div class="own-keep-b">坚持学习 / 天</div>
            </van-col>
            <van-col :span="12">
                <div class="own-keep">
                    0
                </div>
                <div class="own-keep-b">学习文章 / 篇</div>
            </van-col>
        </van-row>-->

        <!--只有学习文章数-->
        <van-row class="user-attribute">
            <van-col :span="12">
                <div class="own-keep" style="padding: 4vw;">
                    <i class="iconfont icon-drxx18" style="font-size: 14vw"></i>
                </div>
            </van-col>
            <van-col :span="12">
                <div class="own-keep">
                    {{readInfo.read_pages}}
                </div>
                <div class="own-keep-b">学习文章 / 篇</div>
            </van-col>
        </van-row>
        <van-cell-group>
            <van-cell icon="question-o" title="学习排行" size="large" to="/rank" is-link/>
            <van-cell icon="service-o" title="联系我们" size="large" to="/groupCode" is-link/>
        </van-cell-group>
       <!-- <img class="user-poster"
             src="https://img.yzcdn.cn/public_files/2017/10/23/8690bb321356070e0b8c4404d087f8fd.png">
        <van-cell-group class="user-group">
            <van-cell icon="records" title="全部订单" is-link/>
        </van-cell-group>

        <van-cell-group>
            <van-cell icon="points" title="我的积分" is-link/>
            <van-cell icon="gold-coin-o" title="我的优惠券" is-link/>
        </van-cell-group>-->
    </div>
</template>

<script>
    import {mapGetters} from "vuex";
    import {Row, Col, Icon, Cell, CellGroup, Skeleton} from 'vant';

    export default {
        name: 'center',
        components: {
            [Skeleton.name]: Skeleton,
            [Row.name]: Row,
            [Col.name]: Col,
            [Icon.name]: Icon,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup
        },
      data() {
        return {
          loading: false,
          orderId: '',
          readInfo: ''
        }
      },
        computed: {
            ...mapGetters(["userInfo"])
        },
        created() {
          this.loading = true;
        },
        mounted() {
          // this.$store.state.user.userInfo
            console.log( this.userInfo)
          this.getUserReadInfo();
          this.loading = false;

        },
        methods: {
            logOut() {
                this.$dialog.confirm({
                    message: '确认要退出登录吗？'
                }).then(() => {
                    // on confirm
                    localStorage.clear();
                    this.$router.replace('/logout')
                }).catch(() => {
                    // on cancel
                });

            },
          async getUserReadInfo() {
            let response = await this.apis.user.getUserReadInfo({openid: this.userInfo.openid});
            console.log(response)
            if(response.code === 200) {
              this.readInfo = response.data;
            }
          }
        }
    };
</script>

<style lang="less">

    .user {
        width: 100%;
        &-poster {
             padding-top: 3vw;
             /*background: cornflowerblue;*/
             background-image:linear-gradient(to right,#b59c88,#806f67);
             color: #fff;
            .poster{
                margin: 3vw 6.5vw;
                width: 18vw;
                height: 18vw;
                img{
                    border-radius: 50%;
                    width: 100%;
                    height: 100%;
                }
            }
            .own-name-line{
                margin-top: 10vw;
                text-align: left;
                .poster-name{
                    font-size: 5vw;
                    font-weight: 600;
                }
            }
        }

        &-attribute {
            height: 24vw;
            padding: 4vw;
            border-radius: 5vw;
            /*background: #3dcc97;*/
            .own-keep {
                font-size: 6vw;
                color: #1cb5cc;
                height: 10vw;
                line-height: 10vw;
            }
            .own-keep-b{
                font-size: 4vw;
                color: #1cb5cc;
                height: 10vw;
                line-height: 10vw;

            }
        }

        &-group {
            margin-bottom: 15px;
        }
        &-links {
            padding: 15px 0;
            font-size: 12px;
            text-align: center;
            background-color: #fff;
            .van-icon {
                display: block;
                font-size: 24px;
            }
        }
    }
</style>