<template>
  <transition name="van-fade">
    <div class="confirmDialog" @click="closeMask" v-if="confirmDialog.show">
      <div class="mask"></div>
      
      <div class="dialogCon">
        <h5 class="van-hairline--bottom" v-if="confirmDialog.title">{{confirmDialog.title}}</h5>
        <p class="msg" v-if="confirmDialog.msg">{{confirmDialog.msg}}</p>
        <div>
          <button @click="confirmDialog.cancelAction" class="own-btn-cancel">{{confirmDialog.cancelText}}</button>
          <button @click="confirmDialog.confirmAction" class="own-btn-confirm">{{confirmDialog.confirmText}}</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  /**
   * 带确认取消按钮的对话框组件，需要传入 confirmDialog 对象，如
   * confirmDialog:{
          show:false,
          msg:'投票不可取消，仅一次投票机会',
          cancelText:'取消',
          confirmText:'投票',
          cancelAction:()=>{
            this.confirmDialog.show = false
          },
          confirmAction:()=>{
            this.confirmAction()
          }
        }
   */
  export default {
    name: 'ConfirmDialog',
    props: {
      confirmDialog: {
        type: Object,
        require: true,
      },
    },
    watch: {
      'confirmDialog.show' () {
        if (this.confirmDialog.show) {
          this.myUtils.forbidBodyScroll()
        } else {
          this.myUtils.allowBodyScroll()
        }
      },
    },
  
    methods: {
      closeMask () {
        this.confirmDialog.show = false;
        this.myUtils.allowBodyScroll()
      },
    },
  }
</script>

<style lang="less" scoped>
  .confirmDialog{
    .mask{
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10000;
      width: 100%;
      height: 100%;
      opacity: .6;
      background: #000;
    }

    .dialogCon{
      top: 50vw;
      width: 80%;
      z-index: 10002;
      margin: 0 10%;
      border-radius: 1vw;
      background: white;
      position: fixed;
      padding: 4vw 1vw;
      h5{
        height: 8vw;
        line-height: 6vw;
        font-size: 5vw;
        font-weight: bold;
        text-align: center;
      }
      .msg{
        height: 12vw;
        line-height: 6vw;
        color: #cc630d;
        font-size: 4vw;
        text-align: left;
        padding: 0 4vw;
        margin-top: 4vw;
      }
      .own-btn-cancel{
        display: block;
        height: 9vw ;
        color: #fff;
        background: #fd7020;
        border-radius: 6vw;
        width: 48vw;
        font-size: 4vw;
        margin: 5vw auto;
      }
      .own-btn-confirm{
        display: block;
        height: 9vw;
        width: 48vw;
        border-radius: 6vw;
        color: #fff;
        margin: 3vw auto;
        background: #3dcc97;
        font-size: 4vw;
      }
    }
  }
</style>