import apiHelper from './apiHelper'

export default {
  getArticleList(params) {
    return apiHelper.pureGet('/apiv1/article/list', {
      ...params
    })
  },
  getPayedArticleList(params) {
    return apiHelper.pureGet('/apiv1/payed/article/list', {
      ...params
    })
  },
  getArticleDetail(params) {
    return apiHelper.pureGet('/apiv1/article/detail', {
      ...params
    })
  }
}
