import './assets/css/core.min.css'
import './assets/css/common.css'

import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import apis from './apis'
import './utils/directive'
import wxShare from './utils/wxShare'
import myUtils from './utils/myUtils'
import {PullRefresh, Lazyload, Button, Toast, List, Tabbar, TabbarItem, Dialog, Cell, Tag, Row, Col, Tab, Tabs, Icon, Rate } from 'vant'
import VueFab from 'vue-float-action-button'
import Vconsole from 'vconsole' // 测试使用console
import wx from 'weixin-js-sdk'
import * as urls from './config/env';
import {
  iconfontUrl,
  iconfontVersion
} from './config/env';

// 移动端调试console
let vConsole = new Vconsole();
export default vConsole;

Vue.config.productionTip = false;
Vue.prototype.apis = apis;
Vue.prototype.wxShare = wxShare;
Vue.prototype.myUtils = myUtils;
Vue.prototype.$wx = wx;
// 加载相关url地址
Object.keys(urls).forEach(key => {
  Vue.prototype[key] = urls[key];
});
// 动态加载阿里云字体库
iconfontVersion.forEach(ele => {
  myUtils.loadStyle(iconfontUrl.replace('$key', ele));
});

Vue.use(PullRefresh)
   .use(Button)
   .use(Toast)
   .use(List)
   .use(Tabbar)
   .use(TabbarItem)
   .use(Dialog)
   .use(Cell)
   .use(Tag)
   .use(Row)
   .use(Col)
   .use(Tab)
   .use(Tabs)
   .use(Icon)
   .use(Rate)
  /* .use(Lazyload, {
      error: require('./assets/img/img_placeholder.svg'),
      loading: require('./assets/img/img_placeholder.svg'),
   })*/
Vue.use(VueFab,  {
  // opitons 可选iconfont图标或MaterialIcons
  iconType: 'MaterialDesign'
  // iconType: 'iconfont'
})
new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
